<h3>1.1. Başlıca Araştırma Konuları</h3>
<br>
<h4>İlişkiler</h4>
<br>
	<h5>Sebep-sonuç ilişkileri</h5>
	<p>Meselâ aşırı yüklenmeyle aşırı ısınma ve arıza, sinyal/gürültü oranının azalması sonucu bilgi kaybı vb.<br>
