	<h5>Ortak bir sebepten kaynaklı ilişkiler</h5>
	<p>Meselâ içme suyu tüketimi artarken doğal gaz tüketiminin azalması doğrudan bir sebep-sonuç ilişkisi olmayıp, ortak sebep hava sıcaklığının artmasından dolayıdır.</p>
	<h5>Ortak ya da zıt sonuca veya amaca yönelik ilişkiler</h5>
	<p>Bu, sebep-sonuç ilişkisinden nadiren ayrılır. Meselâ mazot fiyatları aşırı artınca yemeklik yağ fiyatları genel fiyat artışından ayrıca artar; çünkü bazı kamyoncular mazot yerine yemeklik yağ yakmayı tercih eder.</p>
	<h5>İlişkisizlik</h5>
	<p>Meselâ nükleer denemelerin artması ile ABD'deki doğum oranının düşmesi arasında yanıltıcı bir ilişki kuranlar olmuştur. Ancak bu, doğum kontrol haplarının aynı yıllarda geliştirilmesinden kaynaklanmaktadır. Yani aralarında bir etkileşim ilişkisi yoktur.</p>
	<h5>İlişkilere açıklama</h5>
	<p>
		<u>Doğru yönde etkenler:</u> Meselâ talep artınca fiyatın artması.
    <br>
		<u>Ters yönde etkenler:</u> Meselâ fiyat düşünce talebin artması.
    <br>
		<u>Etkenlerin rekabeti:</u> Meselâ elektrik motorunun gerilimini artırmak hızlandırıcı etki yaparken uyartım akımının artırılması azaltıcı etki yapar. Her ikisi de artarken bunların etkileri gözden kaçabilir.
    <br>
		<p>Grafik üzerinden açıklama ararken hangi değişken bağımsız(sebep) kabul edilerek grafiğin çıkartıldığına dikkat etmek gerekir. Meselâ bağımsız değişken fiyat ise talep-fiyat eğrisi negatif eğimlidir (ters yönlü ilişki). Yani fiyat düşünce müşteri "alayım bari" der ve talep artar. Ama talep bağımsız değişken alınarak çıkarılan fiyat-talep eğrisi pozitif eğimlidir (doğru yönlü ilişki). Yani talep artınca satıcı fırsattan istifade fiyatı artırır. Eğer fiyat bağımsız değişkenine göre çıkarılan eğriyi, bağımsız değişken talepmiş gibi okursanız, satıcının, "talep çok, fiyatları düşüreyim bari" diye kazancını azaltmak istediği gibi saçma bir sonuca varırsınız.</p>
		<p>Benzer bir durum tork-hız eğrileri için de geçerlidir. Motorun ürettiği torkun hıza göre eğrisi negatif eğimli olup bağımsız değişken hızdır. Yani hız düştüğü için motorun ürettiği tork artar. <s>Motor daha çok tork ürettiği için hız düşer</s> diye saçma bir yoruma varılmamalıdır. Yükün aldığı torkun hıza göre eğrisi ise pozitif eğimli olup "alınan tork arttığı için hız artar" diye yorumlanır.</p>
		<u>Sayısal olmayan davranış ilişkileri:</u> Meselâ, alınan alkol ile özgüven arasındaki ilişki, ya da bilgi düzeyi ile tevazu arasındaki ilişki gibi. Aslında bunlar üzerinde yapılan araştırmaların bile "bilimsel" nitelik kazanabilmesi için ölçülebilir kriterler tanımlanması gerekir. Ama subjektif de olsa bu tür ilişkiler hakkında faydalı bilgiler ortaya konabilir.
	</p><br>
	<p>İlişki bulmanın araştırma-geliştirmede çok faydalı bir konusu "duyarlılık analizi" olup, sonraki bir konumuzdur.</p>
