		<p><u>Önceden geliştirilmiş tekniklerin başka alanlarda kullanılması:</u> Meselâ dedikodu teoreminin, torrent uygulamalarında kullanılması.</p>
		<p><u>Ucuzlayan veya iyileştirilen tekniklerin kullanılması:</u> Meselâ GSM modüllerinin, ölçümlerin uzaktan alınmasında kullanılması. Meselâ vida diş kalitesinin artmasıyla CNC tezgâh kalitesinin artırılması.</p>
		<p><u>Zekâ veya emek ürünü yöntemler:</u> Yeni kontrol yöntemleri veya üretim teknolojileri geliştirilmesi gibi.</p>
<br>
<p>Bu araştırma konu türleri birbirinden net sınırlarla ayrılamamış olabilir. Bu iç içe geçiş, bizim imkânlarımızın kısıtlı olmasından da kaynaklanabilir. Meselâ aslında doğruluğu test edilebilir pek çok bulguyu kendimiz ayrıca denemekle zaman kaybetmek istemediğimiz için "naklî bilgi" tarzında alıp kullanırız.</p>

<h3>1.2. Araştırma-Geliştirme Aşamaları</h3>
<br>
<h4>Literatür taraması</h4>
	<p>
	Araştırmaya genellikle literatür taramasıyla başlamak gerekir. Mevcut uygulamalar, geliştirmeler hem yazılı hem görsel kaynaklarda taranır. Başlıca çalışmalar ve onlarda gösterilen kaynaklar üzerinden daha faydalı literatür özeti çıkarılabilir. En çok atıf alan veya izlenen çalışmaların o alandaki başlıca çalışmalar olduğunu düşünsek de bu şart değildir. Bazı değerli çalışmalar yıllar sonra fark edilmekte, birçoğu ise unutulup gitmektedir. Bu yüzden hızlı ve isabetli değerlendirme için çalışmanın "Özet" ve "Sonuç" kısımlarını okuyup kendimiz karar vermemiz en doğrusudur. En azından işine yarayıp yaramayacağını yorumlayacak kadar anlayarak okumak gerekir. ("Okumak" derken duruma göre dinlemek, izlemek, gözlemek de aynı kapsamdadır.) İşimize yarayacağını düşündüğümüz yayınları dikkatlice okumalıyız. Okurken hangi bilgileri aradığımızı unutmamalı, gereksiz ayrıntılarda boğulmamalı, gereken ayrıntıları ise atlamamalıyız.<br>
	<br>
	Literatür özeti, "falan şunu yapmış, filan bunu yapmış" düzeniyle kopuk kopuk sunulmamalıdır. Gelişim sırası ve mantıksal ilişkilere göre "şunu falan şöyle yapmış, bunu filan böyle yapmış" düzeniyle sunulmalıdır. Yani kimin ne yaptığı değil, neyi kimin nasıl yaptığı önemlidir.
	</p><br>
<h4>Teorik temelin güçlendirilmesi</h4>
  <p>
	Literatür taraması sonucu önemli olduğu anlaşılan çalışmalara iyice çalışılmalı, matematiksel anlatımlar, devre ve programların çalışması vb iyice kavranarak öğrenilmelidir. Mevcut başlıca birkaç yöntemin karşılaştırması yapılarak birbirine göre avantaj ve dezavantajları kavranmalıdır.<br>
	<br>
	Bunlar yapılmadan geliştirme yapma ihtimali çok düşük olduğu gibi, yapılsa bile ileride fark edilecek zorluklar daha çok zaman kaybettirebilir. Mevcut çalışmaları deneysel olarak tekrarlamak genellikle fazla zaman ve masraf gerektirdiğinden tercih edilmez. Fakat mümkünse simülasyon ortamında gerçekleştirilmesi oldukça faydalı olur.<br>
	<br>
	"Simülasyon" daha sonraki bir konumuzdur.
  </p>
	<br>
<h4>Teorik geliştirme</h4>
  <p>
	<i>Bu adım, nadiren ve pratik yönü oldukça ağır basan çalışmalarda atlanabilir veya pratik geliştirme sonrasına bırakılabilir (önce pratik geliştirip açıklamasını sonra aramak).</i><br>
	<br>
	Tasarım veya yöntemler önce kâğıt üstünde planlanır. Sonra bilgisayar simülasyonlarıyla doğrulanması tavsiye edilir. Çünkü genellikle simülasyon ortamında yanlışlanma, deneysel olarak da yanlışlanacağını ispatlar. Aksi ise pek doğru sayılmaz; yani simülasyon ortamında doğrulanma, deneysel olarak doğrulanacağını garanti etmez. Bu yüzden simülasyonla yanlışlanan bir fikri deneysel ortamda da test etmek genellikle zaman kaybı olur.
	<br>
  </p>
	<h4>Deneysel araştırma-geliştirme</h4>
  <p>
	<i>Bu adım, çalışmanın konu icabı teori veya yazılımdan ibaret olduğu bazı alanlarda yer almaz.</i><br>
	<br>
	Simülasyon ortamında veya kâğıt üzerinde tasarımlarda başarılı görünen fikirler deneysel ortamda da doğrulanmaya genellikle muhtaçtır. Çünkü bazı pratik engellerin gözardı edilmiş olma ihtimali vardır. Meselâ simülasyon nedenselliğe aykırı, veya ölçülemeyecek bilgiler kullanılarak yapılmış olabilir. Gürültü gibi belirsizlik taşıyan bilgiler çok hassas biliniyor varsayılmış olabilir. Başka bazı mantık hataları da olabilir.<br>
	<br>
	Deneysel çalışmanın ilk denemelerde başarısız olması, denenen fikrin yanlış olduğu anlamına gelmeyebilir. Pek çok deneme hatası yapılmış olabilir. <a href="/projeders/aty1_konu04_hata_bulmak.htm">Hata bulma yöntemleri</a> kullanılarak fikrin çalıştırılması için ısrar edilir. İyi bir teori ile pratiğin uymaması prensip olarak mümkün değildir; yani tüm hata ayıklamalarına rağmen uyuşmazlık görünüyorsa işte bu durum yeni tecrübe kazanacağınız durum olup, teorinizi iyileştirmeniz gerektiği içindir. Hesaba katmadığınız bir şeyleri daha (gürültü analizi gibi) teorinize katmalısınız.
	<br><br>
	Deneyde kullanılan teçhizatın kalitesi çok önemli olabilmektedir. Kalitesiz teçhizat, belirsizlikleri çok artırabilir, yanıltabilir, çalışmaları aksatabilir.
	<br><br>
	Deneydeki başarısızlığın, fikrin kötü olmasından kaynaklandığı anlaşılsa bile hemen pes edilmemeli, takviye edici ya da en baştan yeni fikirler üreterek başarılı sonuçlar çıkarmaya çalışılmalıdır. Deney düzeneği kurmuş ve tecrübe kazanmış olmanın bir şekilde meyvesini almak istenir.
  </p>
<br>
<h4>Sonuçları toparlama</h4>
	<p>
	Araştırma-geliştirme çalışmaları genellikle sürekli daha iyiye doğru devam ettirilebilecek türdendir. Ancak bir yerde durup sonuçları toparlayarak raporlayıp veya ürüne dönüştürüp sunmak şarttır. Aksi halde yapılan çalışmaların kayda geçmesi de takdir edilmesi de mümkün olmaz.
	<br><br>
	Toparlama aşamasında çok muhtemeldir ki hemen hemen bütün deneylerin veya simülasyonların hızlıca tekrarlanması gerekecektir. O yüzden başlangıçta düzenekleri veya yazılımları hazırlarken bu hızlı tekrara uygun şekilde hazırlamalıydınız. Meselâ sonuçları aldınız, bilgisayarda grafik çizdirdiniz, etiketler, çizgi renkleri, boyutlandırmalar vs ile uğraştınız... Derken bir de baktınız ki sonuçları alırken bir şeyi unutmuşsunuz. Tüm bunları yeniden yaparken hamaliye işlerle en az uğraşacak şekilde düzenleme yapmalıydınız, özellikle yazılımda.
	<br><br>
	Toparlanan sonuçlar yorumlanmalıdır. Yorumlar, karşılaştırmalar dürüst ve adil olmalıdır. Yorumsuz sonuç yığını sunmak, özellikle yorumsuz sunulan durumlar müstesna, hiç hoş değildir, adeta emekleri ziyan etmek olur.
</p>
<br>
<h4>Raporlama ve sunum</h4>
	<p>
